$ti-font-family: 'tabler-icons-outline' !default;
$ti-font-path: './fonts' !default;
$ti-font-display: null !default;
$ti-prefix: 'ti' !default;

@font-face {
 font-family: $ti-font-family;
 font-style: normal;
 font-weight: 400;
 font-display: $ti-font-display;
 src:
 url('#{$ti-font-path}/tabler-icons-outline.woff2?v3.0.1') format('woff2'),
 url('#{$ti-font-path}/tabler-icons-outline.woff?') format('woff'),
 url('#{$ti-font-path}/tabler-icons-outline.ttf?v3.0.1') format('truetype');
}

.#{$ti-prefix} {
 font-family: $ti-font-family !important;
 speak: none;
 font-style: normal;
 font-weight: normal;
 font-variant: normal;
 text-transform: none;
 line-height: 1;

 /* Better Font Rendering */
 -webkit-font-smoothing: antialiased;
 -moz-osx-font-smoothing: grayscale;
}

@function unicode($str) {
 @return unquote('"') + unquote(str-insert($str, '\\', 1)) + unquote('"');
}

