 url('#{$ti-font-path}/tabler-icons-filled.woff2?v3.0.1') format('woff2'),
 url('#{$ti-font-path}/tabler-icons-filled.woff?') format('woff'),
 url('#{$ti-font-path}/tabler-icons-filled.ttf?v3.0.1') format('truetype');
}

.#{$ti-prefix} {
